<template lang="html">
  <div class="_full_router bankCardsBody">
    <custHeader :title="title"></custHeader>
    <ul style="margin-top:10px;">
      <mt-cell-swipe  class="cards" v-for="(item,index) in bankCards"
        :right="[
          {
            content:'解绑',
            style:{background:'red',color:'#fff',lineHeight:'64px',fontSize:'14px',fontWeight:'bold'},
            handler:() => {unBund(item.bankAcct)}
          }
        ]">
        <li>
          <div style="margin-top: 10px;">{{ item.bankTitle }}</div>
          <div style="margin-top: 15px;">{{ item.bankAcct | bankcard}}</div>
          <!-- <a :href=item.href>{{ item.cardsTitle }}</a> -->
        </li>
      </mt-cell-swipe>

      <!-- <li class="cards" v-for="(item,index) in bankCards">
        <span>{{ item.bankTitle }}</span>
        <span>{{ item.bankAcct }}</span>
        <a :href=item.href>{{ item.cardsTitle }}</a>
      </li>-->
      <li class="cards"><a href="#/personal/bankCardsAdd">+添加银行卡</a></li>
    </ul>
  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {CellSwipe,MessageBox} from "mint-ui"
Vue.component(CellSwipe.name,CellSwipe)
export default {
  data(){
    return {
      title:'银行卡管理',
      bankCards:[]
    }
  },
  methods:{
    unBund:function(sno){
      console.log(sno)
      MessageBox.confirm("您确定要解绑么").then(action => {
        axiosUtil.upCltBankAcctCancel({bankAcct:sno}).then(data => {
          // var data = data.data;
          if(data.errorcode){
            MessageBox("提示", data.errormsg);
          }else{
            MessageBox.alert("解绑成功").then(action => {
              router.push("/personal")
            });
          }
        });
      });

    }
  },
  created(){
    var self = this;
    axiosUtil.upCltFundAppQry().then(function(data){

        var data = data.data.data
        for (var i = 0; i < data.length; i++) {
          self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct,bankTitle:data[i].orgName})
          // self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct})
        }



    })
  },
  components:{
     custHeader
  },
}
</script>

<style lang="css" scoped>
.bankCardsBody{
  background-color: #eee;
  position: fixed;
  top: 0;
  z-index: 5;
  width: 100%;
}
.cards{
  height: 60px;
  text-align: center;
  line-height: 60px;
  background-color: #fff;
  border-radius: 8px;
  margin: 10px;
  font-size: 22px;
}
</style>



